<template>
    <div class="recommend">
        <Title>推荐歌单</Title>
        <ul class="recommendList">
            <router-link :to="'/songlist/'+v.id" tag="li" v-for="v in recommendMusicList" :key="v.id">
                <div>
                    <img :src="v.picUrl" alt="">
                    <span>{{v.playCount|formatNum}}</span>
                </div>
                <p>{{v.name |substr(26)}}</p>
            </router-link>
        </ul>
        <Loading v-if="recommendMusicList.length<=0"/>
        <Title>最新音乐</Title>
        <MusicItem :newMusicList="newMusicList"></MusicItem>
    </div>
</template>
<script>
import Title from '../components/Title'
import MusicItem from "../components/MusicItem.vue"
import Loading from "../components/Loading"
export default {
    name:"Recommend",
    components:{
        Title,
        MusicItem,
        Loading
    },
    data(){
         return {
             recommendMusicList:[],
             newMusicList:[]
         }
    },
    beforeRouteEnter(to,from,next){
        next(vm=>{
             vm.$axios("/personalized?limit=6").then(data=>{
                 vm.recommendMusicList=data.data.result;
            });
             vm.$axios("/personalized/newsong").then(data=>{
                 vm.newMusicList=data.data.result;
            });
        });
    },
    filters:{
        formatNum(value){
            return (value/10000).toFixed(1)+"万"
        },
        substr(value,len){
            return value.substr(0,len)+"..."
        }
    }
}
</script>
<style lang="less" scoped>
    .recommend{
        border-top:1px solid #eee;
    }
    ul.recommendList{
        
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 16px;
        li{
            width: 33%;
            margin-bottom:16px ;
            div{
                position: relative;
                span{
                    text-indent: 1.2em;
                    top: 2px;
                    right: 3px;
                    position: absolute;
                    color: white;
                    text-shadow: 1px 1px 3px rgba(0, 0,0,0.5);
                    font-size: 12px;
                    // background-image: url(data:image/svg+xml;base64,);
                    // background-repeat: no-repeat;
                }
            }
            p{
                text-align: left;
                font-size: 12px;
                word-wrap: none;
            }
            

        }
    }
</style>